export function photoCompress (file, w, objDiv) {
  var ready = new FileReader()
  /* BlobFile. ,readyStateDONE,onloadend,.,resultdata: URL. */
  ready.readAsDataURL(file)
  ready.onload = function () {
    var re = this.result
    canvasDataURL(re, w, objDiv)
  }
}
function canvasDataURL (path, obj, callback) {
  var img = new Image()
  img.src = path
  img.onload = function () {
    var that = this
    // 
    var w = that.width
    var h = that.height
    var scale = w / h
    w = obj.width || w
    h = obj.height || (w / scale)
    var quality = 0.3 // 0.7
    // canvas
    var canvas = document.createElement('canvas')
    var ctx = canvas.getContext('2d')
    // 
    var anw = document.createAttribute('width')
    anw.nodeValue = w
    var anh = document.createAttribute('height')
    anh.nodeValue = h
    canvas.setAttributeNode(anw)
    canvas.setAttributeNode(anh)
    ctx.drawImage(that, 0, 0, w, h)
    // 
    if (obj.quality && obj.quality <= 1 && obj.quality > 0) {
      quality = obj.quality
    }
    // quality
    var base64 = canvas.toDataURL('image/jpeg', quality)

    // base64file
    const arr = base64.split(','); const mime = arr[0].match(/:(.*?);/)[1] // blob
    const bstr = atob(arr[1]); let n = bstr.length; const u8arr = new Uint8Array(n)
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n)
    }
    const files = new window.File([new Blob([u8arr], { type: mime })], 'test.jpeg', { type: 'image/jpeg' }) // file
    callback(files) // 
  }
}
