<template>
  <div class='user-profile-page'>
    <van-nav-bar left-arrow @click-left="$router.back()" title=""></van-nav-bar>
    <van-cell-group>
      <van-cell is-link title="" center @click="showPhoto=true">
        <van-image
          slot="default"
          fit="cover"
          round
          :src="userInfo.photo"
        />
      </van-cell>
      <van-cell is-link title="" :value="userInfo.name" @click="showName=true;name=userInfo.name" />
    </van-cell-group>
    <van-cell-group style="margin-top:12px">
      <van-cell is-link title="" :value="userInfo.gender===0?'':''" @click="showGender=true"/>
      <van-cell is-link title="" :value="userInfo.birthday || ''" @click="showBirthday=true;birthday=new Date(userInfo.birthday)" />
    </van-cell-group>
    <div class="logout">
      <span @click="logout"></span>
    </div>
    <!--  -->
    <van-popup class="my-popup" v-model="showName" position="right">
      <van-nav-bar
        left-arrow
        @click-left="showName=false"
        right-text=""
        @click-right="modifyNickName"
        title=""
      />
      <van-field v-model="name"></van-field>
    </van-popup>
    <!--  -->
    <van-action-sheet v-model="showGender" :actions="[{ name: '' }, { name: '' }]" cancel-text=""
  close-on-click-action
   @select="onSelect" />

   <!--  -->
       <!--  -->
    <van-popup v-model="showBirthday" position="bottom">
      <van-datetime-picker
        v-model="birthday"
        type="date"
        title=""
        :min-date="minDate"
        :max-date="maxDate"
        @cancel="showBirthday=false"
        @confirm="saveBirthday"
      />
    </van-popup>

    <!-- - -->
    <input type="file" style="display:none" ref="file" @change="updateImage">
    <van-action-sheet
      v-model="showPhoto"
      :actions="[{ name: '', value: 0 },{ name: '', value: 1 }]"
      @select="onPhotoSelect"
      cancel-text=""
    />
  </div>
</template>
<script>
import {
  getUserInfo,
  editUserInfo,
  editUserPhoto
} from '@/api/user'
import { photoCompress } from '@/utils/compress.js'
import dayjs from 'dayjs'
export default {
  name: 'UserProfilePage',
  data () {
    return {
      userInfo: {},
      showName: false,
      name: '',
      showGender: false,
      showBirthday: false,
      birthday: new Date(),
      minDate: new Date('1990/1/1'),
      maxDate: new Date('2030/1/1'),
      showPhoto: false
    }
  },
  created () {
    this.getUserInfo()
  },
  methods: {
    updateImage () {
      // this.$refs.file.files[0]
      photoCompress(this.$refs.file.files[0], { quality: 0.1 }, async (res) => {
        var fd = new FormData()
        fd.append('photo', res)
        const [err, data] = await editUserPhoto(fd)
        if (err) return this.$toast.fail(err)
        console.log(data)
        // actionsheet
        this.showPhoto = false
        this.getUserInfo()
      })
    },
    // 
    onPhotoSelect (obj) {
      console.log(obj.name)
      if (obj.name === '') {
        // 
      } else {
        // ,
        this.$refs.file.click()
      }
    },
    // 
    async saveBirthday () {
      const tempUserUser = Object.assign({}, this.userInfo)
      // 
      tempUserUser.birthday = dayjs(this.birthday).format('YYYY-MM-DD')
      await this.saveUserInfo(tempUserUser)

      // 
      this.showBirthday = false
    },
    // x
    async onSelect (obj) {
    //   console.log(obj.name)
      const tempUserUser = Object.assign({}, this.userInfo)
      if (obj.name === '') {
        tempUserUser.gender = 0
      } else {
        tempUserUser.gender = 1
      }
      await this.saveUserInfo(tempUserUser)
    },
    // 
    async getUserInfo () {
      const [err, data] = await getUserInfo()
      if (err) return this.$toast.fail(err)
      //   console.log(data)
      this.userInfo = data.data.data
    },
    // 
    async modifyNickName () {
      const tempUserUser = Object.assign({}, this.userInfo)
      tempUserUser.name = this.name
      await this.saveUserInfo(tempUserUser)
      // 1.name
      this.text = ''
      // 2.
      this.showName = false
    },
    // 
    async saveUserInfo (userInfo) {
      const [err, data] = await editUserInfo(userInfo)
      if (err) this.$toast.fail(err)
      console.log(data)
      // 3.
      this.getUserInfo()
    },
    logout () {
      this.$dialog.confirm({
        title: '',
        message: '',
        theme: 'round-button'
      }).then(() => {
        console.log('')
        // 1.token
        this.$store.commit('user/setToken', '')
        // 2.
        this.$router.replace('/')
        this.$toast.success('')
      }, () => {
        console.log('')
      })
    }
  }
}
</script>
<style lang="less" scoped>
.user-profile-page {
  background: #f8f8f8;
  .van-image {
    display: block;
    float: right;
    width: 30px;
    height: 30px;
  }
  .van-cell__title {
    width: 50px;
    flex: none;
  }
}
.logout {
  text-align: center;
  position: absolute;
  left: 0;
  bottom: 30px;
  width: 100%;
  color: @geek-color;
}
.my-popup {
  width: 100%;
  height: 100%;
  /deep/ .van-nav-bar__text {
    color: @geek-color;
  }
  /deep/ .van-field__control {
    background: @geek-gray-color;
    padding: 10px;
    border-radius: 4px;
  }
}
</style>
